import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);
export default new Vuex.Store({
    strict: true,
    //需要中间管理的数据
    state:{
        //存储的值
        val:"",
        //存储token
        userToken:"",
        //目前的开发模式(1是开发，0是线上)
        isDevMode:0
    },
    //可以认为是 store 的计算属性
    getters:{
        //获取当前开发模式的值
        getIsDevModeFn:(state)=>{
            return state.isDevMode;
        }
        // getWiFiList: (state) => {
        //     return state.mapPoint;
        // }
        //获取shopInfo店铺信息
        // getShopInfo: (state)=>{
        //     return state.shopInfo;
        // }
    },
    //在这里创建改变state里数据的方法
    mutations:{
        //改变目前开发模式的方法
        changeDevModeFn(state, value){
            console.log(value);
            state.isDevMode=value;
        },
        //改变orderList的方法
        // changeOrderList(state, value){
        //     console.log(value);
        //     state.val=value;
        // }
    },
    actions:{

    }
});


